import React from 'react';
import { Slide } from 'react-slideshow-image';
import 'react-slideshow-image/dist/styles.css'
import { category, projectArch } from '../data';

const spanStyle = {
  padding: '20px',
  background: '#efefef',
  color: '#000000'
}

const divStyle = {
  display: 'flex',
  borderradius:'30px',
  alignItems: 'center',
  justifyContent: 'center',
  backgroundSize: 'cover',
  height: '20vh'
}

export default function Slideshow(){

    return (
        <div className="slide-container slideshow">
        <Slide>
         {category.map((category)=> (
           <div key={category.title}>
               <p className='center'>shortcut to my projects</p>
              <div style={{ ...divStyle, 'backgroundImage': `url(${category.src})` }}>
                <span style={spanStyle}>{category.title}</span>
              </div>
            </div>
          ))} 
        </Slide>
      </div>
    )
}

